<template>
  <div class="recommend">
    <div class="recommend-content">
      <div class="slider-wrap">
        <slider>
          <!--<div v-for="item in recommends">
            <a :href="item.linkUrl">
              <img :src="item.picUrl" alt="">
            </a>
          </div>-->
        </slider>
      </div>
      <div class="recommend-list">
        <h1 class="list-title">热门歌单推荐</h1>
        <ul>
          <li class="item" v-for="item in discList">
            <div class="icon">
              <img :src="item.imgurl" width="60" height="60">
            </div>
            <div class="text">
              <h2 class="name" v-html="item.creator.name"></h2>
              <p class="desc" v-html="item.dissname"></p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import { getDiscList } from 'api/recommend'
  import Slider from 'base/swiper-slider/slider'
  export default {
    components: {
      Slider
    },
    data () {
      return {
        recommends: [],
        discList: []
      }
    },
    created () {
      this._getDiscList()
    },
    mounted () {
    },
    methods: {
      _getDiscList () {
        getDiscList().then((res) => {
          this.discList = res.data.list
        })
      }
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
@import "../../common/scss/common";
  .recommend{
    flex: 1;
    overflow: scroll;
    .recommend-list{
      .list-title{
        text-align: center;
        color: $color-theme;
        font-size: $font-size-medium;
        height: 65px;
        line-height: 65px;
      }
      .item{
        display: flex;
        box-sizing: border-box;
        align-items: center;
        padding: 0 20px 20px 20px;
        .icon{
          flex: 0 0 60px;
          width: 60px;
          margin-right: 20px;
        }
        .text{
          display: flex;
          flex-direction: column;
          justify-content: center;
          flex: 1;
          line-height: 20px;
          overflow: hidden;
          font-size: $font-size-medium;
          .name{
            margin-bottom: 10px;
            color: $color-text;
          }
          .desc{
            color: $color-text-d
          }
        }
      }
    }
  }
</style>



